@import "variables.scss";
@import "mixins.scss";
.Contact {
  position: relative;
  overflow: hidden;
  flex: 0 1 25%;
  height: 100%;
  box-shadow: -5px -5px 10px rgba($color-white, 0.8),
    5px 5px 15px rgba($color-black, 0.4);
  border-radius: 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1.5rem;
  margin: 1.5rem;
  text-decoration: none;
  color: $color-black;
  transition: all 0.3s;
  @include respond(tab-port) {
    width: 80vw;
    padding: 1.5rem 0;
  }
  &:hover {
    transform: translateY(-0.5rem) scale(1.1);
  }

  &__img-box {
    width: 5rem;
    height: 5rem;
    margin-bottom: 1.5rem;
    @include respond(tab-port) {
      width: 8rem;
      height: 8rem;
    }
  }

  &__img {
    width: 100%;
    filter: drop-shadow(-3px -3px 2px rgba($color-white, 0.3))
      drop-shadow(5px 5px 10px rgba($color-black, 0.2))
      drop-shadow(10px 10px 10px rgba($color-black, 0.2))
      drop-shadow(15px 15px 10px rgba($color-black, 0.2));
  }

  &__text {
    font-size: 2rem;
    @include respond(tab-land) {
      font-size: 1.5rem;
    }
  }
}
